
$margin: 16px;
$double: $margin * 2;
$half: $margin / 2;
$quarter: $margin / 4;
$opf: $half * 3;

$font: 'IBM Plex Mono', monospace;

$header-height: 64px;

$light: #eee;
$dark: #222;
$shadow: 0 0 $margin #00000080;

$ease: cubic-bezier(0.23, 1, 0.32, 1);
$transition: all 0.32s $ease;

$col-width: 400px;
$col-width-margin: calc(#{$col-width} - #{$double});
$tablet: '(min-width: 1024px)';
$laptop: '(min-width: 1280px)';
