
@import "../../theme/Theme.module.scss";


.root {
  color: white;
  a {
    color: $dark;
    text-decoration: none !important;
  }
  > header {
    transition: background-color 320ms $ease;
    background: #FFFFFF18;
    box-shadow: none;
    z-index: 1000;
    position: fixed;
    top: 0; left: 0;
    width: calc(100vw - 32px);
    height: $header-height;
    display: flex;
    align-items: center;
    padding: 0 $margin;
    > h3 {
      margin: 0;
    }
    > div {
      display: none;
    }
    > i {
      margin-left: auto;
      justify-self: flex-end;
    }
    @media #{$laptop} {
      width: calc(100vw - 42px);
      > div {
        display: inline-block;
        padding-top: 3px;
        margin-left: auto;
        justify-self: flex-end;
        > a {
          color: white;
          font-weight: bold;
          margin: 0 $margin;
        }
        > a:last-child {
          margin-right: 0;
        }
      }
      > i {
        display: none;
      }
    }
  }
}

.mobileMenu {
  background: #FFF;
  color: black;
  z-index: 1001;
  position: fixed;
  text-align: right;
  top: $half; right: $half;
  width: auto;
  min-height: 200px;
  box-shadow: $shadow;
  padding-bottom: $half;
  > a, > i {
    font-weight: bold;
    display: block;
    padding: $half $margin;
    cursor: pointer;
  }
  > a:hover, > i:hover {
    background: $light;
  }
  > i {
    padding: $margin;
  }
}

.isScrolled {
  > header {
    color: $dark;
    background: #FFFFFF;
    box-shadow: $shadow;
    a {
      color: $dark !important;
    }
  }
}

