
@import "globals.scss";

html, body {
  width: 100vw;
  margin: 0;
  padding: 0;
  overflow-x: hidden;
  font-family: $font;
}

h1 {
  font-size: 1.5rem;
  overflow: hidden;
}

.center {
  text-align: center;
}

.route {
  padding: $margin;
  padding-top: $header-height;
}

.code {
  padding: 0 2px;
  border: 1px solid rgba(0,0,0,.1);
  border-radius: 2px;
  background-color: $light;
  color: #e01e5a;
}

.displayNone {
  display:none;
}

.section {
  padding: $margin;
  > p {
    line-height: 1.5rem;
    overflow: hidden;
  }
  > button {
    margin-top: $margin;
    background: white;
    border-radius: 4px;
    border: none;
    padding: $margin;
    box-shadow: 0 0 16px #FFFFFF80;
  }
}

.section:first-child {
  padding-top: 0;
}

@media #{$laptop} {
  .section {
    vertical-align: top;
    display: inline-block;
    width: calc(50% - 32px);
  }
    
  .route {
    padding-right: 10px;
  }
}