
@import "../../theme/Theme.module.scss";


.root {
  display: flex;
  flex-wrap: wrap;
  > section {
    margin-top: $double;
    position: relative;
    flex: 0 0 100%;
    a[name] {
      position: absolute;
      top: -64px;
    }
  }
  @media #{$laptop} {
    > section {
      padding: $double;
      flex: 0 0 calc(50% - 64px);
    }
    .singleCenter {
      margin:auto;
      align-self: center;
      flex: 0 0 50% !important;
    }
    .fullWidth {
      margin:auto;
      align-self: center;
      flex: 0 0 100%;
      display: flex;
      flex-wrap: wrap;
      > div {
        flex: 0 0 33%;
        align-self: center;
        margin:auto;
        margin-left: -48px;
      }
    }
  }
  ul {
    padding: 0 $margin;
  }
  ol {
    padding: 0 28px;
  }
  img {
    width: 100%;
  }
}

.logo {
  text-align: center;
  div:nth-child(2) {
    margin-left: -32px;
  }
  div:nth-child(3) {
    height: 120px;
  }
  img {
    max-width: 350px;
  }
}

